import styled from 'styled-components';

export const ButtonRed = styled.button`
    width: 134px;
    height: 4opx;
    border-style: none;
    border-radius: 8px;
    color: white;
    font-size: 18px;
    background-color: #FF3C4A;
    padding: 8px;
    margin:10px;
    &:hover{
        background-color: #cc242f;
    } 

`;

export default ButtonRed;
